* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'DM Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
    color: #333;
    line-height: 1.6;
}

.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 0 20px;
}

.container-wide {
    padding: 20px 20px;
}

/* Sections */
section {
    padding: 60px 0;
}

h1 {
    font-family: 'DM Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
    text-align: center;
    font-size: 2.5rem;
    font-weight: 500;
    letter-spacing: 0.05em;
}

h2 {
    font-size: 2rem;
    margin-bottom: 30px;
    font-weight: 400;
}

h3 {
    font-family: 'DM Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
    text-align: center;
    font-weight: 400;
}

p {
    margin-bottom: 15px;
    font-size: 1rem;
    color: #555;
}

/* Calendar */
.calendar-widget {
    margin: 40px auto;
    max-width: 400px;
    padding: 20px;
}

.calendar-controls {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.calendar-nav {
    background: none;
    border: 1px solid #ddd;
    padding: 5px 15px;
    cursor: pointer;
    font-size: 1.2rem;
    color: #333;
    border-radius: 4px;
}

.calendar-nav:hover {
    background: #f5f5f5;
}

.calendar-month {
    margin-bottom: 20px;
}

.calendar-month h3 {
    font-size: 1.2rem;
    margin-bottom: 15px;
    font-weight: 600;
}

.calendar-weekdays {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 5px;
    margin-bottom: 10px;
    font-size: 0.85rem;
    color: #666;
    font-weight: 500;
}

.calendar-weekdays div {
    text-align: center;
    padding: 5px 0;
}

.calendar-days {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 5px;
}

.calendar-day {
    aspect-ratio: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.9rem;
    border: 1px solid #eee;
    border-radius: 4px;
    background: white;
    color: #333;
}

.calendar-day.empty {
    border: none;
    background: transparent;
}

.calendar-day.past {
    color: #ccc;
    background: #f9f9f9;
}

.calendar-day.open {
    background: #333 !important;
    color: white !important;
    font-weight: 600;
    border-color: #333;
}

.calendar-day.open.past {
    background: #333 !important;
    color: white !important;
}

.calendar-legend {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 15px;
    font-size: 0.9rem;
}

.legend-item {
    display: flex;
    align-items: center;
    gap: 8px;
}

.legend-dot {
    width: 16px;
    height: 16px;
    border-radius: 4px;
    display: inline-block;
}

.legend-dot.open {
    background: #333;
}

/* Menu */
.menu-section {
    padding: 10px 0;
}

.menu-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 60px;
}

.menu-column {
    text-align: center;
}

.menu-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.menu-item {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
    border-bottom: 1px solid #eee;
}

.menu-name {
    font-size: 1rem;
    color: #333;
}

.menu-price {
    font-size: 1rem;
    color: #666;
}

/* Contact */
.contact {
    text-align: center;
}

/* Footer */
.footer {
    border-top: 1px solid #eee;
    padding: 30px 0;
    text-align: center;
    color: #999;
    font-size: 0.9rem;
}

/* Responsive */
@media (max-width: 768px) {
    h1 {
        font-size: 2rem;
    }

    h2 {
        font-size: 1.5rem;
    }

    .menu-grid {
        grid-template-columns: 1fr;
        gap: 40px;
    }
}

/* Navigation */
.app-layout {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.top-nav {
    background: #f8f8f8;
    border-bottom: 1px solid #eee;
    padding: 15px 20px;
    position: sticky;
    top: 0;
    z-index: 100;
}

.top-nav-links {
    list-style: none;
    display: flex;
    gap: 30px;
    justify-content: center;
}

.top-nav-links a {
    color: #333;
    text-decoration: none;
    padding: 5px 0;
}

.top-nav-links a:hover,
.top-nav-links a.active {
    border-bottom: 2px solid #333;
}

.main-content {
    flex: 1;
}


/* Image Grid */
.image-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 80px;
    margin-top: 30px;
    max-width: 1100px;
    margin-left: auto;
    margin-right: auto;
}

.image-grid img {
    width: 100%;
    aspect-ratio: 3/4;
    object-fit: cover;
    border-radius: 8px;
}

.image-card p {
    margin: 5px 0 0;
    padding: 0;
    text-align: center;
    color: #333;
}

.image-card {
    text-decoration: none;
    color: inherit;
}

/* Dish Page - Lookbook Style */
.lookbook {
    min-height: 100vh;
}

.lookbook-content {
    padding: 40px;
    position: fixed;
    top: 53px;
    left: 0;
    width: 50%;
    height: calc(100vh - 53px);
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lookbook-content > * {
    width: 70%;
}

.lookbook-title {
    font-size: 2rem;
    font-weight: 400;
    letter-spacing: 0.05em;
    margin-bottom: 15px;
    text-align: left;
}

.lookbook-description {
    margin-bottom: 30px;
}

.lookbook-recipe h2 {
    font-size: 1.2rem;
    margin: 30px 0 15px;
    font-weight: 500;
}

.lookbook-recipe ul {
    padding-left: 20px;
}

.lookbook-recipe li {
    margin-bottom: 8px;
}

.lookbook-recipe p {
    line-height: 1.8;
}

.lookbook-media {
    position: fixed;
    top: 53px;
    right: 0;
    width: 50%;
    height: calc(100vh - 53px);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: min-content;
    gap: 15px;
    padding: 40px;
    overflow-y: auto;
    align-content: start;
}

.lookbook-media img,
.lookbook-media video {
    width: 100%;
    aspect-ratio: 0.9;
    object-fit: cover;
    display: block;
    border-radius: 12px;
}

@media (max-width: 768px) {
    .lookbook {
        display: flex;
        flex-direction: column-reverse;
    }

    .lookbook-content {
        position: static;
        width: 100%;
        height: auto;
        padding: 40px 20px;
    }

    .lookbook-media {
        position: static;
        margin-left: 0;
        width: 100%;
        height: auto;
        padding: 20px;
        align-content: start;
    }
}


/* Breadcrumbs */
.breadcrumbs {
    padding: 10px 20px;
    font-size: 0.85rem;
    color: #666;
}

.breadcrumbs a {
    color: #666;
    text-decoration: none;
}

.breadcrumbs a:hover {
    text-decoration: underline;
}

.breadcrumb-sep {
    margin: 0 8px;
}


/* Hover Cat */
.image-card {
    text-decoration: none;
    color: inherit;
}

.image-card-thumb {
    position: relative;
    line-height: 0;
}

.image-card-thumb::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.8);
    opacity: 0;
    transition: opacity 0.2s;
    border-radius: 8px;
    pointer-events: none;
}

.image-card-thumb::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('https://res.cloudinary.com/dlenbkeui/image/upload/v1769062386/mew_transparent_eb9002.png') center/50% no-repeat;
    opacity: 0;
    transition: opacity 0.2s;
    border-radius: 8px;
    pointer-events: none;
}

.image-card:hover .image-card-thumb::before,
.image-card:hover .image-card-thumb::after {
    opacity: 1;
}


/* Marquee */
.marquee {
    overflow: hidden;
    white-space: nowrap;
    padding: 20px 0;
}

.marquee span {
    display: inline-block;
    animation: marquee 40s linear infinite;
    font-size: 15rem;
    font-weight: 500;
}

.marquee.reverse span {
    animation: marquee-reverse 40s linear infinite;
}

@keyframes marquee {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-50%);
    }
}

@keyframes marquee-reverse {
    0% {
        transform: translateX(-50%);
    }
    100% {
        transform: translateX(0);
    }
}
